import React, { Component } from 'react'
import Login from "./Login"
import Register from "./Register"

export class Form extends Component {

    constructor(props){
        super(props);
        this.state = {
          view: 'login'
        }
    }

    goRegister(){
      this.setState({
        view: 'register'
      }, () => console.log(this.state.view))
    }

    goLogin(){
      this.setState({
        view: 'login'
      })
    }

    render() {
      return(
        <div>
        {this.state.view === 'login'
        ? <Login view={this.goRegister.bind(this)} />
        : <Register view={this.goLogin.bind(this)} />
        }
        {/* <button onClick={this.goRegister.bind(this)}> TRY THIS! </button> */}
        </div>
      );

      // if(this.state.view === 'login'){
        //   return(<Login
        //     view={this.changeView.bind(this)}
        //   />)
        // } else if (this.state.view === 'register'){
        //   return(<Register
        //     view={this.goLogin.bind(this)}
        //   />)
        // }
    }
}

export default Form
